import style from './style.module.css';
import { Fade } from 'react-slideshow-image';
import Photo1 from './HomePhoto.jpg';
import Photo2 from './HomePhoto2.jpg';
import Photo3 from './HomePhoto3.jpg';
import Photo4 from './HomePhoto4.jpg';


const fadeProperties = {
    autoplay: true,
    arrows: false,
  };


const Greetings = (props) =>{
    return (
        <div className = {style.wrapper}>
            <div className = {style.buyTicket}>
                <p><a href = '#' className = {style.buyTicketLink} data-tc-event="5fc3b553a5a8c0e77e30dc56" data-tc-meta="true" data-tc-token="eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiIsImlzcyI6InRpY2tldHNjbG91ZC5ydSJ9.eyJwIjoiNWU0NTcyNzM4M2U4NWFmMDA5NjEyNGM1In0.3jCPA4_UvbVKUhrYJUR4pQTiNyrtyzIx4xD5vHJk-dU">купить билет</a></p>
            </div>
            <div className = {style.carouselWrapper}>
                <Fade {...fadeProperties}>
            <div>
            <img src={Photo1} className = {style.carouselPhoto}/>
        </div>
        <div >
            <img src={Photo2} className = {style.carouselPhoto}/>
        </div>
        <div >
            <img src={Photo3} className = {style.carouselPhoto}/>
        </div>
        <div >
            <img src={Photo4} className = {style.carouselPhoto}/>
        </div>
      </Fade>
        </div>
            <div className = {style.textWrapper}>
                <h2 className = {style.mainTextTop}>TRIPLE F</h2>
                <h2 className = {style.mainTextMiddle}>СВОБОДНАЯ</h2>
                <h2 className = {style.mainTextBottom}>ГИК-ЯРМАРКА</h2>
            </div>

        <div className = {style.buttonWrapper}>
            <button className = {style.ticketButton}>
                КУПИТЬ БИЛЕТ
            </button>
        </div>
        <div className = {style.more}>
            <h2 className = {style.moreText}>УЗНАТЬ БОЛЬШЕ</h2>
        </div>
        </div>
    );
}

export default Greetings;