import style from './style.module.css';
import Logo28 from './28Logo.png';
import DoramaLogo from './DoramaLogo.png';
import FuzeteaLogo from './FuzeteaLogo.png';
import IviLogo from './IviLogo.png';
import MosigraLogo from './MosigraLogo.png';
import RedBullLogo from './RedBullLogo.png';
import SkyengLogo from './SkyengLogo.png';
import VkusvillLogo from './VkusvillLogo.png';
import VsemaykiruLogo from './VsemaykiruLogo.png';

const Tickets = (props) =>{
    return (
        <div className = {style.wrapper}>
             <div className = {style.labelWrapper}>
                 
                <h2 className = {style.label}>МЫ СОТРУДНИЧАЛИ С: </h2>
             </div>
             <div className = {style.sponsorWrapper}>
                 <div className = {style.logoItem28}>
                     <img className = {style.logoImg} src = {Logo28} alt = "logo"/>
                 </div>
                 <div className = {style.logoItemDorama}>
                     <img className = {style.logoImg} src = {DoramaLogo} alt = "logo"/>
                 </div>
                 <div className = {style.logoItemFuzetea}>
                     <img className = {style.logoImg} src = {FuzeteaLogo} alt = "logo"/>
                 </div>
                 <div className = {style.logoItemIvi}>
                     <img className = {style.logoImg} src = {IviLogo} alt = "logo"/>
                 </div>
                 <div className = {style.logoItemMosigra}>
                     <img className = {style.logoImg} src = {MosigraLogo} alt = "logo"/>
                 </div>
                 <div className = {style.logoItemRedBull}>
                     <img className = {style.logoImg} src = {RedBullLogo} alt = "logo"/>
                 </div>
                 <div className = {style.logoItemSkyeng}>
                     <img className = {style.logoImg} src = {SkyengLogo} alt = "logo"/>
                 </div>
                 <div className = {style.logoItemVkusvill}>
                     <img className = {style.logoImg} src = {VkusvillLogo} alt = "logo"/>
                 </div>
                 <div className = {style.logoItemVsemaykiru}>
                     <img className = {style.logoImg} src = {VsemaykiruLogo} alt = "logo"/>
                 </div>
             </div>
        </div>
    );
}

export default Tickets;